.skills {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 80px 0;
}

.skills__header {
  text-align: center;
  background-color: rgb(24, 33, 83);
  padding: 40px;
  color: white;
  letter-spacing: 1px;
  border-bottom: 1px solid white;
}

.skills__background {
  background-image: url('https://static.wixstatic.com/media/baac51_5f3719a2964e482ebdf6c889115744c0~mv2_d_4896_3264_s_4_2.jpg/v1/fill/w_1395,h_425,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/baac51_5f3719a2964e482ebdf6c889115744c0~mv2_d_4896_3264_s_4_2.jpg');
  width: 100%;
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  z-index: -1;
}

.skills__background_filter {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 22, 78, 0.686);
}

.skills__items {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: center;
  align-items: center;
  padding: 0px 200px;
  margin-top: 50px;
}

.skills__item {
  width: 400px;
  line-height: 80px;
  background-color: rgba(255, 255, 255, 0.128);
  color: white;
  font-size: 20px;
  text-align: center;
  transition: .3s all;
  opacity: 0;
}

.skills__item_active {
  opacity: 1;
}

.skills__item:hover {
  background-color: rgba(255, 255, 255, 0.264);
}

@media only screen and (max-width: 1250px) {
  .skills__item {
    width: 300px;
  }
}

@media only screen and (max-width: 1050px) {
  .skills {
    padding: 80px 0 40px;
  }

  .skills__background {
    height: 830px;
  }
}

@media only screen and (max-width: 700px) {
  .skills__items {
    padding: 0px 100px;
  }

  .skills__item {
    line-height: 60px;
    font-size: 16px;
  }
}

@media only screen and (max-width: 450px) {
  .skills__items {
    padding: 0px 65px;
  }
}